import { getStoreDetail, getTradeCoupon, submitTrade } from '@/api/biunique'
import config from "@/config/common.config"
import CourseCard from '@/components/backgroundPromote/CourseCard'
import CourseOrder from '@/components/CourseDetail/CourseOrder'
export default {
  components:{
    CourseCard,
    CourseOrder
  },
  data(){
    return {
      data: {
        cardList:[]
      },
      initData: false,
      radio:null, //优惠券选择
      config,
    }
  },
  mounted(){
    this.getCourseStatus()
  },
  methods:{
    //获取课程状态
    async getCourseStatus() {
      const { data } = await getStoreDetail(this.$route.params.id)
      const { data:{data:{cardList}} } = await getTradeCoupon()
      if( data.code === 0 ) {
        this.data = {
          ...data.data,
          id:this.$route.params.id,
          cardList:cardList
        }
        if(this.data.cardList.length> 0) {
          const {card, money} =this.data.cardList.reduce((p, v) => p.money < v.money ? v : p)
          this.radio = card
          this.couponValue = money
        }
        this.initData = true
      }
    },
    //订单支付跳转
    async buyCourse() {
      let params = {storeId: this.$route.params.id}
      if(this.radio) {
        const index = this.data.cardList.findIndex(p=>p.card === this.radio)
        if(index > -1) {
          params.card = this.data.cardList[index].card
        }
      }
      const { data } = await submitTrade(params)
      if(data.code === 0){
        this.$router.push({
          path:`/promoteQuestion/${this.data.id}?tradeNO=${data.data.tradeNO}`
        })
      }
    }
  },
  head () {
    return {
      title: '订单确认—艾思云课堂'
    }
  }
}
